import ActionIcon from 'components/ActionIcon';
import DeleteIcon from '@mui/icons-material/Delete';
import api from '../Api';

export const DeleteTitle = 'Delete Memory';
export const DeleteConfirmMessage = 'Do you want to delete this memory?';

export function DeleteMemoryIcon() {
  return (
    <ActionIcon icon={<DeleteIcon />} label="Delete" />
  );
}

export default async function deleteMemory(bucket, authURL, memoryID, memoryDeleteCallback) {
  try {
    const deleteResults = await api.deleteMemory(bucket, authURL, memoryID);
    memoryDeleteCallback(deleteResults);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}
