import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Confirm from 'components/confirmation';
import UpdateMemoryIcon from 'components/UpdateMemoryIcon';
import { ShareTitle, ShareConfirmMessage, ShareMemoryIcon } from './Share';
import { DeleteTitle, DeleteConfirmMessage, DeleteMemoryIcon } from './Delete';

function ConfirmAction({
  title, message, callback, open, closeCallback,
}) {
  const [confirm, setConfirm] = useState(open);
  useEffect(() => setConfirm(open), [open]);

  return (
    <Confirm
      title={title}
      confirm={confirm}
      handleCallback={callback}
      handleCloseCallback={() => {
        setConfirm(false);
        closeCallback();
      }}
      message={message}
    />
  );
}

export default function MemoryActions({
  snackBar, handleDeleteObject, handleEditClick,
}) {
  let confirmAction;
  const [anchorEl, setAnchorEl] = useState(null);
  const [action, setAction] = useState(null);

  const open = Boolean(anchorEl);
  const handleClick = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  if (action === 'delete') {
    confirmAction = (
      <ConfirmAction
        open
        title={DeleteTitle}
        message={DeleteConfirmMessage}
        callback={handleDeleteObject}
        closeCallback={() => setAction(null)}
      />
    );
  }

  if (action === 'share') {
    confirmAction = (
      <ConfirmAction
        open
        title={ShareTitle}
        message={ShareConfirmMessage}
        callback={() => {}}
        closeCallback={() => setAction(null)}
      />
    );
  }

  const handleDeleteClick = () => {
    setAction('delete');
    handleClose();
  };

  const handleShareClick = () => {
    setAction('share');
    handleClose();
  };

  const handleEdit = () => {
    handleEditClick();
    handleClose();
  };

  return (
    <div>
      <MoreVertIcon onClick={handleClick} />
      <Menu
        id="memory-actions"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
        sx={{ borderRadius: '10px' }}
      >
        <MenuItem onClick={handleEdit}><UpdateMemoryIcon /></MenuItem>
        <MenuItem onClick={handleShareClick}><ShareMemoryIcon /></MenuItem>
        <MenuItem onClick={handleDeleteClick}><DeleteMemoryIcon /></MenuItem>
      </Menu>
      {confirmAction}
      {snackBar}
    </div>
  );
}

MemoryActions.propTypes = {
  snackBar: PropTypes.node.isRequired,
  handleDeleteObject: PropTypes.func.isRequired,
  handleEditClick: PropTypes.func.isRequired,
};

ConfirmAction.propTypes = {
  open: PropTypes.bool.isRequired,
  title: PropTypes.string.isRequired,
  message: PropTypes.string.isRequired,
  callback: PropTypes.func.isRequired,
  closeCallback: PropTypes.func.isRequired,
};
